  borla: ['f. Conjunto de hebras o cordoncillos en forma de media bola sujeto por uno de sus cabos.', 'Conjunto de plumas unidas circularmente para empolvarse el cutis: colorete con borla.'],
  borro: ['tr. y prnl. Hacer desaparecer lo trazado por cualquier medio: se ha borrado o han borrado esa firma?', 'Desvanecer,  eliminar,  hacer que desaparezca totalmente una cosa: he borrado ese incidente de mi memoria.'],
