  braza: ['f. Medida de longitud equivalente a 2 varas o 1, 6718 m.', 'Estilo de natacin en que los hombros se mantienen a nivel de la superficie del agua,  los brazos se mueven de delante atrs mientras las piernas se encogen y estiran: la braza es el estilo ms rpido de aprender.'],
