  brial: ['m. Antiguo vestido femenino largo y de seda: el brial es una tnica talar de mangas estrechas.', 'Antiguo faldn de tela hasta las rodillas que usaban los soldados:  coloc la cota sobre su brial rojo.'],
  broca: ['f. Barrena torneada sin manija de las taladradoras: necesito una broca del ocho para este taco.', 'Carrete con hilo que hace la trama de ciertos tejidos en la lanzadera del telar.'],
  brota: ['intr. Nacer la planta de la tierra.', 'Salir en la planta renuevos,  flores,  hojas,  etc.: los olmos han brotado de nuevo esta primavera.'],
