  broto: ['intr. Nacer la planta de la tierra.', 'Salir en la planta renuevos,  flores,  hojas,  etc.: los olmos han brotado de nuevo esta primavera.'],
  broza: ['f. Conjunto de hojas,  ramas y despojos de las plantas,  as como la maleza que surge en el campo: limpi los colmenares de broza.', 'Desecho de alguna cosa.'],
  bruja: ['f. Vase brujo,  ja.'],
