  bruma: ['f. Niebla,  en especial,  la que se forma sobre el mar: la bruma de Londres.'],
  bruta: ['adj. Necio,  incapaz. Tambin s.: eres un bruto,  no entiendes nada.', '[Persona] sin educacin,  grosera y sin moderacin: procura comportarte y no ser tan bruto.'],
  bruto: ['adj. Necio,  incapaz. Tambin s.: eres un bruto,  no entiendes nada.', '[Persona] sin educacin,  grosera y sin moderacin: procura comportarte y no ser tan bruto.'],
  bruza: ['f. Cepillo espeso de fuertes cerdas con una abrazadera de cuero para la mano,  que sirve para limpiar las caballeras,  los moldes de imprenta,  etc.: pasaba la bruza a su yegua con mimo.'],
