  burra: ['m. y f. asno,   mamfero cuadrpedo: Platero es un burro peludo y suave.', 'adj. y s. [Persona] necia e ignorante: hay que ser burro para hacer ese examen.'],
  burro: ['m. y f. asno,   mamfero cuadrpedo: Platero es un burro peludo y suave.', 'adj. y s. [Persona] necia e ignorante: hay que ser burro para hacer ese examen.'],
  busto: ['m. Parte superior del cuerpo humano,  desde el cuello a la cintura: tiene un busto griego,  equilibrado.', 'Escultura o pintura de la cabeza y parte superior del trax: busto de Csar.'],
