  cague: ['m. vulg. Miedo: esa pelcula le da cague.'],
  caite: ['intr. y prnl. Desplazarse un cuerpo de arriba abajo por la accin de su propio peso: cay desde el octavo piso.', 'Perder un cuerpo el equilibrio: se cay al subir las escaleras.'],
  calca: ['tr. Obtener una copia de un trazo o escritura por medio de un papel transparente o de calco: calc los dibujos del bordado.', 'Imitar o reproducir con exactitud: ha calcado los gestos de su ta.  Se conj. como sacar.'],
