  cania: ['adj. col. Tpico,  folclrico: Espaa ca.', 'adj. y com. [Gitano] de raza: tez ca.  pl. cas.'],
  canje: ['m. Cambio,  trueque o sustitucin: te propongo un canje,  tu coleccin de vdeos por la ma de cmics antiguos.'],
  canoa: ['f. Embarcacin de remo o con motor,  estrecha,  sin quilla y generalmente de una pieza. La proa y la popa tienen la misma forma y suelen terminar en punta: los indgenas cruzaban el ro en canoa.'],
  canso: ['tr. y prnl. Causar cansancio,  fatigar: subir las escaleras ya me cansa menos.', 'Aburrir,  hartar: comer todos los das pollo acaba cansando.'],
  canta: ['m. Composicin potica con msica a propsito para ser cantada,  o adaptable a los aires populares: el Nuevo Mester de Juglara recupera viejos cantares.', 'cantar de gesta Poesa popular de la poca medieval en que se referan,  en principio oralmente,  hechos de personajes histricos,  legendarios o tradicionales: el "Poema de Mio Cid" es un cantar de gesta.'],
  cante: ['m. Cualquier tipo de canto popular,  especialmente el andaluz: es un entendido en cante y baile flamenco.', 'col. Olor fuerte y molesto: vaya cante a sudor!'],
  caoba: ['f. rbol americano de la famila de las meliceas,  de hasta de 30 m de altura,  con tronco recto y grueso,  hojas compuestas,  flores pequeas y blancas y fruto capsular y leoso. Su madera es muy estimada para fabricar muebles,  por su color rojizo y por poderse pulimentar fcilmente.', 'Madera de este rbol.'],
  capar: ['tr. Extirpar o inutilizar los rganos genitales,  castrar: antiguamente se capaba a los hombres que iban a ejercer de eunucos en un harn.'],
