  cardo: ['m. Planta compuesta de aproximadamente 1 m de altura,  con hojas grandes y espinosas como las de la alcachofa,  flores azules en cabezuela y pencas que se comen crudas o cocidas.', 'Nombre comn de diversas especies de plantas silvestres,  compuestas y de hojas espinosas: cardo borriquero,  estrellado.'],
  carea: ['m. Acto que enfrenta a varias personas para interrogarlas a la vez y observar sus reacciones,  con frontar sus opiniones y as averiguar la verdad de los hechos: en el juicio ha habido varios careos entre los acusados.'],
  careo: ['m. Acto que enfrenta a varias personas para interrogarlas a la vez y observar sus reacciones,  con frontar sus opiniones y as averiguar la verdad de los hechos: en el juicio ha habido varios careos entre los acusados.'],
  carey: ['m. Reptil quelonio marino de aproximadamente 1 m de longitud que habita en los mares tropicales,  con las extremidades anteriores ms largas que las posteriores. Los huevos de esta tortuga se consideran un manjar y las placas crneas de su caparazn se utilizan en la realizacin de diversos objetos decorativos.', 'Materia crnea obtenida del caparazn de esta tortuga: peine de carey.  pl. careys o careyes.'],
