  carpo: ['m. anat. Conjunto de huesos dispuestos en dos filas,  de nmero variable,  que forman parte del esqueleto de las extremidades anteriores que articulan el antebrazo con el metacarpo. En el cuerpo humano son ocho huesos y conforman la mueca.'],
  carra: ['m. Carruaje de dos o cuatro ruedas,  con varas para enganchar el tiro y tablas para sostener la carga.', 'Pieza de la mquina de escribir en la que va el rodillo con el papel y que se desplaza de un lado a otro.'],
  carst: ['m. karst.  No vara en pl.'],
