  cebar: ['tr. Engordar a un animal,  especialmente cuando se desea aprovechar su carne para el consumo: estuvieron cebando al pavo de la cena de Navidad durante casi dos meses.', 'P. ext.,  alimentar a una persona abundantemente: tu madre te est cebando. Tambin prnl.'],
  cebra: ['f. Mamfero perisodctilo africano parecido al asno,  de pelo blanco amarillento con rayas transversales pardas o negras.', 'paso de cebra Paso de peatones marcado por franjas blancas o amarillas en la calzada: cuando me examin para sacar el carn de conducir me suspendieron por no parar en un paso de cebra.'],
