  clima: ['m. Conjunto de condiciones atmosfricas propias de una zona geogrfica: clima lluvioso,  tropical.', 'Ambiente,  conjunto de condiciones que caracterizan la situacin o la circunstancia que rodea a una persona: clima familiar,  poltico.'],
  cloro: ['m. qum. Elemento qumico metaloide gaseoso,  verde,  de olor fuerte y muy txico. Se emplea como blanqueador y como desinfectante. Su smbolo es Cl,   y su nmero atmico,  17.'],
