  criba: ['f. Utensilio consistente en una lmina agujereada o una tela sujeta a un aro de madera,  que se emplea para separar granos de distintos tamaos o cosas similares.', 'Seleccin o eleccin de lo que interesa: despus de la primera criba,  los seleccionados deben hacer una entrevista personal.'],
