  curda: ['f. Borrachera: nos cogimos una gran curda.', 'com. Borracho: solo quedaba un pobre curda tirado en la calle.'],
  curdo: ['adj. y s. kurdo.'],
  curia: ['f. Grupo de abogados,  procuradores y funcionarios que trabajan en la administracin de justicia.', 'Organismos e instituciones que colaboran en el gobierno de la Iglesia: prefera ser un simple practicante a formar parte de la curia.'],
