  curry: ['(voz i.) m. Condimento en polvo para salsas,  carnes y pescados,  muy picante,  hecho de cilantro,  canela,  jengibre,  pimienta de cayena,  pimienta,  clavo,  nuez moscada y crcuma,  que es su principal excipiente: el pollo al curry es un plato tpico hind.'],
