  dalia: ['f. Planta compuesta de mediana altura,  con tallo ramoso,  hojas opuestas y dentadas y grandes inflorescencias con el botn central amarillo y la corola de muchos ptalos y variados colores: la dalia es una planta de origen mexicano.', 'Flor de esta planta: le regal un ramo de dalias.'],
  dance: ['intr. Bailar al ritmo de la msica. Tambin tr.: danzar una mazurca.', 'intr. Moverse de un lado para otro con rapidez y agitacin: pasa el da danzando y termina agotado.  Se conj. como cazar.'],
