  debut: ['m. Primera presentacin en pblico de una obra o artista: este es su debut en los escenarios madrileos.', 'P. ext.,  primera actuacin de alguien en cualquier actividad: tuvo un debut espectacular en el mundo de las finanzas.  pl. debuts.'],
