  desde: ['prep. Indica el punto,  procedencia u origen en el tiempo y en el espacio: lo esperaba desde ayer; llam desde Pars; empieza a contar desde cero.', 'desde luego loc. adv. Por supuesto: desde luego que os alojaris en mi casa.'],
