  dique: ['m. Muro artificial hecho para contener la fuerza de las aguas o del oleaje: construyeron un dique para contener el desbordamiento del ro.', 'Recinto cerrado en la orilla de una drsena en donde se limpian y reparan los barcos cuando baja la marea: dique seco.'],
