  duela: ['f. Cada una de las tablas curvadas de un barril,  tonel o cuba.', 'Nombre comn de diversos gusanos platelmintos,  aplanados y de forma casi ovalada,  con una ventosa en el extremo anterior del cuerpo,  en cuyo centro est la boca,  y otra en la parte inferior del animal. Viven parsitos en los conductos biliares del carnero y del toro: la duela del hgado puede parasitar excepcionalmente al hombre.'],
