  fanal: ['m. Farol grande que se coloca en las torres de los puertos para que su luz sirva de seal nocturna: pudieron arribar sin contratiempos en mitad de la tormenta gracias al fanal.', 'Campana de cristal para resguardar algo: guarda sus valiosas figuras de porcelana bajo un fanal.'],
