  feudo: ['m. En la Edad Media,  contrato por el cual los soberanos y los grandes seores concedan tierras u otros bienes a sus vasallos a cambio de que ellos y sus descendientes les prestaran servicios y les jurasen fidelidad: el feudo obligaba a los vasallos a ser fieles a los seores feudales.', 'Tierra o dominio que se concede en feudo: los campesinos medievales explotaban los feudos que les concedan los grandes seores,  pero no eran propietarios de las tierras.'],
