  menos: ['adv. comp. Denota idea de falta, disminucin, restriccin o inferioridad en comparacin expresa o sobreentendida: gasta menos; s menos altivo; Juan es menos prudente que su hermano.  Se construye tambin con el artculo determinado: este cuadro es el menos colorista de la exposicin.', 'Denota a veces limitacin indeterminada de cantidad expresa: en la batalla murieron menos de 100 hombres.'],
