  merma: ['f. Disminucin o consuncin de algo: merma de volumen.', 'Porcin que se consume o extrae de algo: la merma de lquido corporal fue excesiva.'],
  mesar: ['tr. y prnl. Arrancar o tirar de los cabellos o barbas: en su desesperacin se mesaba el pelo.'],
  metal: ['m. qum. Cada uno de los elementos qumicos que componen un grupo, caracterizados por ser buenos conductores del calor y de la electricidad, tener un brillo caracterstico y ser slidos a temperatura ambiente, salvo el mercurio: el calcio es un metal.', 'Aleacin industrial: refuerzo de metal.'],
  meter: ['tr. Introducir o hacer penetrar una cosa dentro de otra o en algn lugar: meter dinero en el banco, meter en la crcel. Tambin prnl.:el ratn se meti en aquel agujero.', 'Enredar, inmiscuir. Tambin prnl.:  siempre se mete donde no le llaman.'],
  metra: ['m. Unidad bsica de medida de longitud del sistema mtrico decimal: un metro equivale a la diezmillonsima parte del cuadrante del meridiano terrestre que pasa por Pars.', 'Instrumento de medida que tiene marcada la longitud de esta unidad y sus divisores: tienes un metro para medir el fondo de la mesa?'],
  micra: ['f. Unidad de longitud equivalente a la millonsima parte del metro: la micra se representa con la  griega.'],
  micro: ['m. col. Apc. de micrfono.'],
  miedo: ['m. Sensacin de alerta y angustia por la presencia de un peligro o mal, sea real o imaginario:  miedo a morir, miedo al fracaso.', 'Recelo de que suceda lo contrario a lo que se espera o desea:  tengo miedo de que llueva el da de la excursin.'],
  migar: ['tr. Desmenuzar o partir el pan en pedazos muy pequeos para hacer migas u otra cosa semejante: mig los mendrugos para hacer un budn de pan.', 'Echar estos pedazos en un lquido: migar magdalenas en la leche.  Se conj. como llegar.'],
  migra: ['intr. Emigrar, cambiar el lugar de residencia.', 'Realizar un animal su migracin peridica: las cigeas han dejado de migrar a Marruecos.'],
  milla: ['f. Medida de longitud marina equivalente a 1.852 m.', 'Medida terrestre inglesa de longitud equivalente a 1.609 m.'],
  millo: ['adj. y pron. num. card. Diez veces cien.', '[Nmero o cantidad] grande o indeterminado: te he dicho mil veces que no puedo ir.'],
  milpa: ['f. amer. maizal.'],
  minar: ['tr. Abrir galeras subterrneas: los topos minan los sembrados.', 'Colocar minas o explosivos ya sea en tierra o en mar: ha minado una franja frente a las trincheras.'],
  mingo: ['m. En el billar, bola que se coloca en la cabecera de la mesa al comenzar cada mano del juego.', 'amer. Juego de nios similar a la petanca que se juega con bolitas.'],
  minio: ['m. Monxido de plomo de color rojizo anaranjado que, diluido en aceite o cido, se usa como pintura o preparacin antioxidante: el minio se usaba en la Edad Media para la ilustracin de manuscritos.'],
  mioma: ['m. pat. Tumor formado por clulas y elementos musculares: mioma intrauterino.'],
  miope: ['adj y com. Que padece miopa.', 'col. Corto de alcances o de miras, poco perspicaz: miope para los negocios.'],
  mirar: ['tr. Fijar la vista en un objeto. Tambin prnl.: se mir en el espejo.', 'Tener por fin u objetivo: solo mira por s mismo.'],
  mirla: ['m. Nombre comn de diversas especies de aves paseriformes de unos 25 cm de largo, de color generalmente negro con pico amarillo el macho y de color pardo la hembra, fcilmente domesticables y de canto aflautado y variado: el mirlo aprende a imitar sonidos humanos.', 'mirlo acutico Nombre comn de diversas aves paseriformes de unos 18 cm de longitud, con el cuerpo rechoncho, plumaje pardo y la garganta y pecho claros: el mirlo vive cerca de cursos de agua de Europa, Asia y frica.'],
  mirlo: ['m. Nombre comn de diversas especies de aves paseriformes de unos 25 cm de largo, de color generalmente negro con pico amarillo el macho y de color pardo la hembra, fcilmente domesticables y de canto aflautado y variado: el mirlo aprende a imitar sonidos humanos.', 'mirlo acutico Nombre comn de diversas aves paseriformes de unos 18 cm de longitud, con el cuerpo rechoncho, plumaje pardo y la garganta y pecho claros: el mirlo vive cerca de cursos de agua de Europa, Asia y frica.'],
  mirra: ['f. Resina gomosa roja y aromtica usada en perfumera y medicina.'],
  mirto: ['m. Planta arbustiva de la familia de las mirtceas, de unos 2 a 3 m de altura, con las ramas flexibles, hojas de color verde vivo, duras y persistentes, flores olorosas pequeas y blancas y fruto en bayas de color negro del que se extrae el aceite y ungento de arrayn.'],
  misal: ['adj. y m. [Libro] litrgico en que se contiene el orden y modo de celebrar la misa.'],
  mismo: ['adj. Idntico, no otro ni diferente: esta es la misma moneda que yo le di.', 'Semejante o igual: usamos la misma talla; tiene la misma voz que su madre.'],
  mitad: ['f. Cada una de las dos partes iguales en que se divide un todo: quieres la mitad del bocadillo?', 'Punto central que equidista de los extremos: ests a mitad de camino.'],
  mitin: ['m. Acto o reunin pblica en la que se discuten asuntos polticos o sociales: presentaron a su candidato en un mitin.', 'dar el mitin loc. col. Dar un escndalo, llamar mucho la atencin: no me vayas a dar el mitin en la puerta del cine.  En algunas zonas de Amrica alterna con la forma mitn.'],
  mitra: ['f. Toca alta y puntiaguda que lleva el Papa, los obispos, arzobispos y otros eclesisticos en las celebraciones solemnes.', 'Dignidad de arzobispo u obispo, y territorio de su jurisdiccin.'],
  miura: ['m. Toro de la ganadera de Miura, famosa por la bravura de sus reses.'],
  mixta: ['adj. Compuesto de elementos heterogneos: matrimonio mixto.', 'm. Sndwich de jamn y queso.'],
  mixto: ['adj. Compuesto de elementos heterogneos: matrimonio mixto.', 'm. Sndwich de jamn y queso.'],
