  mogol: ['adj. y s. mongol.', 'gran mogol Ttulo que reciben los soberanos de una dinasta mahometana de la India: el gran mogol recibi a los embajadores.'],
  mojar: ['tr. Humedecer con agua, lquido o una materia semilquida: has mojado el mantel con el vino. Tambin prnl.:se me han mojado los zapatos.', 'col. Beber para celebrar algo: esto hay que mojarlo.'],
  molde: ['m. Objeto hueco que da forma a la materia que en l se solidifica: molde de yeso; molde de tarta.', 'Esquema, norma: su estilo escapa a moldes.'],
  moler: ['tr. Reducir un cuerpo slido a polvo o pequeas partculas por presin o friccin: muelen el trigo en la piedra.', 'Cansar o fatigar mucho fsicamente: esta mudanza nos ha molido a todos. Tambin intr.'],
  molla: ['f. Parte magra de la carne: le sirvi la molla del cocido.', 'Miga del pan.'],
  molle: ['m. Molcula gramo, cantidad de sustancia cuya masa en gramos es igual a su masa atmica: un mol contiene tantas entidades como tomos hay en 0,012 kg de carbono 12.'],
  momia: ['f. Cadver desecado por medios naturales o artificiales, que se ha conservado sin corromperse: momia egipcia.', 'Persona muy delgada y demacrada.'],
