  monje: ['m. Religioso que pertenece a una orden monacal cuyos integrantes viven en comunidad en monasterios: los monjes budistas viven en comunidades.', 'Anacoreta, individuo que vive retirado, dedicado a la oracin y la penitencia: el monje le bendijo en su camino.'],
  monta: ['f. Arte de montar a caballo: escuela de monta.', 'Valor, calidad o estimacin de una cosa:  politiquillo de poca monta.'],
