  morar: ['intr. Residir, vivir habitualmente en un lugar: en aquella mansin moraban nuestros antepasados.'],
  morbo: ['m. Tendencia obsesiva hacia lo desagradable, lo cruel, lo prohibido: esta pelcula tiene mucho morbo.'],
  morfa: ["pref. y suf. que expresan la idea de 'forma': morfologa, antropomorfo."],
  morir: ['intr. Dejar de vivir: ha muerto despus de una larga enfermedad. Tambin prnl.', 'Finalizar o extinguirse algo completamente: su recuerdo no morir. Tambin prnl.'],
  morse: ['m. Sistema telegrfico que utiliza un alfabeto convencional a base de puntos y rayas.', 'Este mismo alfabeto: cdigo morse.'],
  mosca: ['f. Nombre que reciben varias especies de insectos dpteros de cuerpo negro, cabeza elptica, alas transparentes cruzadas de nervios, patas largas con uas y ventosas y boca en forma de trompa para chupar y alimentarse.', 'Pelo que nace entre el labio inferior y el comienzo de la barbilla del hombre: afeitarse la mosca.'],
  mosco: ['m. amer. mosca.'],
