  motel: ['m. Establecimiento hotelero de carretera, generalmente formado por pequeos apartamentos independientes.'],
  motor: ['adj. Que produce movimiento: rgano motor.', 'Que consigue que algo funcione bien: el elemento motor de nuestra empresa ha sido la informatizacin de las tareas.'],
  mucho: ['adj. Abundante, numeroso, intenso, que sobrepasa lo normal: tiene muchos amigos; hace mucho fro.', 'adv. c. En alto grado, en cantidad elevada: me ha gustado mucho la novela.'],
  mudez: ['f. Imposibilidad fsica para hablar: la mudez que tiene se debe a un trauma infantil.', 'Silencio premeditado y deliberado: su mudez como consecuencia del enfado no dur ms de dos horas.'],
  mueca: ['f. Contorsin del rostro para expresar alguna emocin o para hacer burla.'],
  muela: ['f. Piedra de molino que gira sobre otra fija para moler lo que hay entre ambas.', 'Piedra de material abrasivo que se utiliza para afilar herramientas: afila el cuchillo en la muela.'],
  muelo: ['tr. Reducir un cuerpo slido a polvo o pequeas partculas por presin o friccin: muelen el trigo en la piedra.', 'Cansar o fatigar mucho fsicamente: esta mudanza nos ha molido a todos. Tambin intr.'],
