  mujer: ['f. Persona del sexo femenino: emancipacin de la mujer.', 'La que ha llegado a la edad de la pubertad.'],
  mular: ['adj. Del mulo, de la mula o relativo a ellos: ganado mular.'],
  mulla: ['tr. Ahuecar y esponjar una cosa: mullir la almohada.', 'Cavar la tierra alrededor de las cepas para ahuecarla.  Irreg. Vase conj. modelo.'],
  multa: ['f. Sancin econmica que se impone por no cumplir la norma: me pusieron una multa por aparcar en una salida de incendios.', 'Papel donde se indica que alguien no ha cumplido una norma, especialmente de circulacin: cuando llegu al coche tena una multa en el parabrisas.'],
  mundo: ['m. Conjunto de todas las cosas creadas: Dios cre el mundo en siete das.', 'El planeta Tierra: el mundo es redondo.'],
