  museo: ['m. Institucin dedicada a la adquisicin, conservacin, estudio y exposicin de objetos artsticos o cientficos para que puedan ser examinados: el Museo del Prado es una de las mejores pinacotecas del mundo.', 'Edificio o lugar en que se guardan y exponen colecciones de objetos artsticos o cientficos con alguna caracterstica comn: museo de la ciencia.'],
