  mutua: ['f. Vase mutuo, tua.'],
  mutuo: ['adj. Recproco: se ofrecieron apoyo mutuo.', 'f. Mutualidad,  sociedad de socorros mutuos: tengo el seguro del coche en una mutua.'],
  nabab: ['m. Gobernador de una provincia en la India mahometana.', 'Hombre sumamente rico.'],
