  nadie: ['pron. indet. Ninguna persona: no quiere ver a nadie.', 'm. Persona insignificante: es un don nadie.'],
  nadir: ['m. astron. Punto de la esfera terrestre diametralmente opuesto al cenit.'],
  nafta: ['f. qum. Lquido incoloro, voltil, ms ligero que el agua y muy combustible, que se utiliza como disolvente industrial: la nafta es una fraccin ligera del petrleo natural que se obtiene en la destilacin de la gasolina como una parte de esta.', 'amer. Gasolina: tenemos que pasar por un surtidor de nafta para repostar.'],
  nahua: ['adj. Del antiguo pueblo indio que habit la altiplanicie mexicana y la parte de Amrica Central antes de la conquista de estos territorios por los espaoles, y que alcanz alto grado de civilizacin, o relacionado con l. Tambin com.', '[Grupo de lenguas] hablado principalmente por los indios mexicanos. Tambin m.'],
  naipe: ['m. Cartulina rectangular que lleva figuras pintadas en una cara y sirve para jugar a las cartas: hizo un juego de manos con unos naipes.', 'pl. Baraja: trae los naipes y jugamos un rato al tute.'],
