  narco: ['com. narcotraficante.'],
  nardo: ['m. Planta amarilidcea de tallo sencillo, hojas radicales y flores blancas muy olorosas: los nardos del jardn huelen mucho durante la noche.', 'Flor de esta planta.'],
  nariz: ['f. Parte saliente del rostro humano, entre la frente y la boca, con dos orificios que comunican con la membrana pituitaria y el aparato de la respiracin. Ms en pl.: se limpi las narices con un pauelo.', 'Sentido del olfato: tiene buena nariz para los perfumes.'],
  nasal: ['adj. Relativo a la nariz: cavidad nasal.', '[Sonido] en cuya pronunciacin la corriente espirada sale total o parcialmente por la nariz: est emitiendo sonidos nasales todo el rato por el catarro que tiene.'],
