  noclo: ['m. Dulce hecho de masa de harina, azcar, manteca, huevos, vino y ans.'],
  noema: ['m. Contenido objetivo del pensar, a diferencia del acto intelectual o noesis.'],
  nogal: ['m. rbol de la familia de la juglandceas, de tronco corto y robusto, corteza resinosa, copa frondosa y redondeada y hojas dentadas, cuyo fruto es la nuez y cuya madera es muy apreciada en ebanistera.', 'Madera de este rbol.'],
  nomon: ['m. gnomon.'],
