  nutra: ['tr. y prnl. Proporcionar a un organismo vivo las sustancias que necesita para realizar sus funciones.', 'Fortalecer, vigorizar, alentar: a menudo, la vida se nutre de ilusiones.'],
  oasis: ['m. Zona con vegetacin y agua que se encuentra aislada en los desiertos arenales de frica y Asia.', 'Tregua, descanso: tras la discusin vivimos un oasis de paz.  No vara en pl.'],
  obeso: ['adj. y s. [Persona] excesivamente gruesa.'],
