  tiara: ['f. Tocado alto con tres coronas,  que remata en una cruz sobre un globo,  que usaron los Papas como smbolo de su autoridad.', 'Dignidad de Sumo Pontfice.'],
  tibia: ['f. anat. Hueso principal y anterior de la pierna,  que se articula con el fmur,  el peron y el astrgalo.', 'zool. Una de las piezas de las patas de los insectos.'],
  tibio: ['adj. Templado,  entre caliente y fro: quiero que el agua de la baera est tibia.', 'Poco intenso y apasionado: hizo una tibia defensa de sus argumentos.'],
