  tifus: ['m. pat. Gnero de enfermedades infecciosas graves,  caracterizadas por provocar fiebre alta,  diarrea,  hemorragias intestinales,  delirio o postracin,  aparicin de costras negras en la boca y a veces presencia de manchas punteadas en la piel.  No vara en pl.'],
  tigre: ['m. y f. Mamfero carnvoro de unos 1, 80 m de longitud ms 1 m de cola,  con la piel de color amarillo con rayas negras en el lomo y la cola y el vientre blancos,  con gran fuerza muscular y agilidad para el salto y poderosas zarpas y mandbulas: el tigre habita principalmente en la India.', 'm. col. Retrete o servicio,  especialmente el de un lugar pblico.'],
  tilde: ['f. Rasgo que se pone sobre algunas letras,  como el que lleva la  o el que denota acentuacin: la tilde diacrtica sirve para diferenciar el significado de algunos monoslabos.', 'Tacha,  cualidad negativa que tiene alguien o que se le atribuye.'],
  timar: ['tr. Quitar o hurtar con engao: me han timado el reloj entre la aglomeracin.', 'Engaar a otro en una compra o contrato: me timaron cuando compr este electrodomstico.'],
  timba: ['f. Partida de juego de azar: me voy a una timba que ha organizado un amigo.', 'Casa de juego,  garito.'],
