  tinto: ['adj. De color rojo oscuro: ese rojo tinto te favorece mucho.', 'Teido: tiene las manos tintas de la sangre de todas sus vctimas.'],
  tiple: ['m. La ms aguda de las voces humanas,  soprano: con esa voz de tiple nunca podr alcanzar tesituras tan bajas.', 'ms. Pequeo instrumento de cuerda,  como una guitarrita pequea,  de sonidos muy agudos: en su coleccin de instrumentos populares hay un tiple.  Es sinnimo de timple.'],
