  tirio: ['adj. y s. De Tiro o relativo a esta antigua ciudad fenicia,  en el actual territorio de Lbano.', 'tirios y troyanos Partidarios de opiniones o intereses opuestos: aquello pareca una reunin de tirios y troyanos.'],
  tisis: ['f. pat. Tuberculosis pulmonar: la tisis fue una enfermedad mortal en el pasado.', 'pat. Cualquier enfermedad en la que el enfermo se consume lentamente,  tiene fiebre y presenta ulceracin en algn rgano.  No vara en pl.'],
  tizna: ['tr. Manchar con tizne,  holln u otra materia. Tambin prnl.: dijo la sartn al cazo "aprtate,  que me tiznas".', 'Deslustrar o manchar la fama o el prestigio de alguien: el escndalo ha tiznado a toda la familia.'],
  tizne: ['amb. Humo u holln que se pega a los objetos que han estado puestos al fuego. Ms c. m.: esta sartn est llena de un tizne horrible.'],
