  torpe: ['adj. Que es de movimiento lento,  tardo y pesado: camina con paso torpe debido al rema.', 'Desmaado,  falto de habilidad y destreza: reconoce que siempre has sido algo torpe para las manualidades.'],
  torre: ['f. Edificio fuerte,  ms alto que ancho: las torres han servido especialmente como defensa o como campanario.', 'Pieza del juego de ajedrez que imita esta construccin: me enroco con la torre.'],
