  trece: ['adj. y pron. num. card. Diez ms tres: trece minutos.', 'adj. num. ord. Que ocupa el lugar nmero trece en una serie ordenada de elementos,  decimotercero: maana celebrar su trece cumpleaos. Tambin m.,  aplicado a los das del mes:el trece de diciembre.'],
