  tulio: ['m. qum. Elemento qumico del grupo de los lantnidos. Es un metal denso,  fcilmente inflamable,  cuyas sales son de color verde grisceo. Su smbolo es Tm,   y su nmero atmico,  69.'],
  tumor: ['m. Hinchazn y bulto que se forma anormalmente en alguna parte del cuerpo: tengo un tumor en un ovario.', 'Alteracin patolgica de un rgano o de una parte de l,  producida por la proliferacin creciente de las clulas que lo componen: tumor canceroso.'],
  tupir: ['tr. y prnl. Apretar mucho una cosa,  hacerla tupida,  compacta: tupir la trama de un tejido.'],
