  ungir: ['tr. Aplicar aceite u otra materia grasa,  extendindola superficialmente.', 'Signar con leo sagrado a una persona,  para denotar el carcter de su dignidad,  o para la recepcin de un sacramento: lo ungieron sacerdote el ao pasado.  Se conj. como rugir.'],
