  yelmo: ['m. Parte de la armadura antigua que resguardaba la cabeza.'],
  yente: ['m. Unidad monetaria de Japn: cuando el yen cae,  todas las bolsas del mundo se resienten.'],
  yerba: ['f. hierba.', 'amer. mate 3.'],
  yermo: ['adj. y m. Inhabitado.', 'Incultivado: da tristeza ver los yermos donde antes hubo sembrados.'],
  yerna: ['f. amer. nuera.'],
  yerno: ['m. Respecto de una persona,  marido de su hija.'],
  yerra: ['tr. e intr. No acertar,  fallar,  equivocarse: el adivino err sus pronsticos.', 'intr. Andar vagando de una parte a otra sin rumbo ni destino:  erraba por las calles sin saber dnde iba a pasar la noche.'],
  yerro: ['m. Equivocacin por descuido o inadvertencia: culp a otro de su yerro.'],
  yerto: ['adj. Tieso,  rgido,  especialmente a causa del fro: cuando descubrieron el cadver,  ya estaba yerto.'],
  yesca: ['f. Materia muy seca y fcilmente inflamable: mi abuelo tena un mechero que funcionaba con yesca.', 'P. ext.,  lo que est muy seco y,  por consiguiente,  dispuesto a encenderse.'],
  yihad: ['(voz rabe) f. Guerra santa de los musulmanes: la yihad tiene como objeto extender el islamismo.'],
  yogui: ['com. Practicante del yoga: los yoguis son personas fsicamente sanas y mentalmente equilibradas.'],
  yogur: ['m. Producto de consistencia cremosa que se obtiene por fermentacin de la leche: me he tomado un yogur con frutas para merendar.'],
  yunga: ['adj. Natural de los valles clidos que hay a ambos lados de los Andes. Tambin com.', 'm. Antigua lengua del norte y centro de la costa peruana.'],
  yunta: ['f. Par de bueyes,  mulas u otros animales que sirven en las labores del campo: las yuntas estn siendo sustituidas por mquinas.', 'amer. Gemelos para poner en los puos de las camisas.'],
  zafar: ['tr. Soltar lo que estaba amarrado o sujeto,  desembarazar,  libertar,  especialmente en el lenguaje marinero. Tambin prnl.', 'prnl. Escaparse o esconderse para evitar un encuentro o riesgo: el delincuente consigui zafarse de la polica.'],
  zafio: ['adj. Tosco,  grosero: modales zafios.'],
  zaina: ['adj. Traidor,  falso: no te puedes fiar de l porque es muy zaino.'],
  zaino: ['adj. Traidor,  falso: no te puedes fiar de l porque es muy zaino.'],
  zalea: ['f. Cuero de oveja o carnero curtido de modo que conserve la lana; sirve para preservar de la humedad y del fro.'],
  zamba: ['f. Danza popular del noroeste de Argentina.', 'Msica y canto de esta danza.'],
  zambo: ['adj. Persona que tiene juntas las rodillas y separadas las piernas hacia fuera: ese nio anda de manera extraa porque es zambo. Tambin s.', 'amer. [Hijo] de negro e india,  o al contrario. Tambin s.'],
  zampa: ['f. Cada una de las estacas que se clavan en un terreno para hacer el firme sobre el que se va a edificar.'],
  zanca: ['f. Parte ms larga de las patas de las aves,  desde los dedos hasta la primera articulacin por encima de ellos.', 'Pierna de persona o de animal,  sobre todo cuando es larga y delgada.'],
  zanco: ['m. Cada uno de los palos altos,  con salientes sobre los que se ponen los pies,  para andar en alto: en el desfile venan unos acrbatas que caminaban sobre zancos.'],
  zanja: ['f. Excavacin larga y estrecha que se hace en la tierra: han hecho una zanja para meter las tuberas del gas.', 'amer. Surco producido por una corriente de agua.'],
  zanjo: ['tr. Resolver un asunto,  o concluirlo: ya han zanjado la venta.', 'Abrir zanjas.'],
  zapar: ['intr. Trabajar con la zapa o pala.'],
  zapeo: ['m. Cambio repetido de canal de televisin utilizando el mando a distancia: durante los anuncios,  todo el mundo hace zapeo.  Se usa ms el trmino ingls zapping.'],
  zarco: ['adj. De color azul claro:  ojos zarcos.'],
  zarpe: ['intr. Levar anclas,  hacerse a la mar un barco desde el lugar en que estaba fondeado o atracado: zarparemos dentro de unas horas hacia Amrica.'],
  zarza: ['f. Nombre comn de diversas especies de arbustos de la familia de las rosceas,  con tallos sarmentosos,  arqueados en las puntas,  de 4 a 5 m de largo,  ramas con espinas,  hojas divididas en cinco hojuelas elpticas,  aserradas,  flores blancas o rosas en racimos terminales,  y cuyo fruto es la zarzamora: las zarzas del pinar dan unas zarzamoras muy dulces.'],
  zarzo: ['m. Tejido de varas,  caas,  mimbres o juncos,  que forma una superficie plana: pusieron un zarzo alrededor de la finca.', 'P. ext.,  objeto que se realiza con este tejido.'],
  zenit: ['m. cenit.'],
  zocas: ['m. En Marruecos y otras ciudades del norte de frica,  lugar en que se celebra un mercado: el zoco de Marrakech es muy pintoresco.'],
  zombi: ['m. En el culto del vud,  persona resucitada que carece de voluntad y se comporta como un autmata: en la pelcula de terror,  unos zombis atacaban a las personas.', 'com. Atontado. Tambin adj.: ests zombi,  no te enteras de nada.'],
  zonal: ['adj. De la zona,  relacionado con ella o que se produce en una zona: defensa zonal.'],
  zonda: ['m. amer. Viento fuerte,  clido y muy seco,  que afecta desfavorablemente a los seres vivos produciendo cierta inquietud y excitacin.'],
  zonzo: ['adj. amer. Tonto,  simple.', '[Persona] sosa o inspida. Tambin s.'],
  zopas: ['com. col. Persona que cecea mucho: este nio es un zopas,  no sabe pronunciar la ese.'],
  zorra: ['f. Vase zorro,  ra.'],
  zorro: ['m. y f. Nombre comn de diversas especies de mamferos carnvoros de menos de 1 m de longitud incluida la cola,  hocico alargado y pelaje color pardo rojizo y muy espeso,  especialmente en la cola,  de punta blanca: un zorro entr en el redil y mat varias ovejas.  Tambin se usa el femenino para referirse al animal genrico.', 'Persona astuta: no logrars engaarle porque es muy zorro.'],
  zotal: ['m. Desinfectante o insecticida que se usa generalmente en establos o para el ganado: el zotal tiene un olor muy caracterstico.'],
  zuavo: ['m. Soldado del cuerpo francs de infantera que estaba en frica en poca colonial: en un principio,  todos los zuavos eran argelinos.'],
  zueca: ['m. Zapato de madera de una pieza que usan en varios pases los campesinos: los zuecos protegen los pies del barro.', 'Zapato de cuero con suela de corcho o de madera,  sin taln: las enfermeras suelen llevar zuecos blancos.'],
