  zumbo: ['intr. Producir una cosa ruido o sonido continuado y sordo: una mosca zumbaba insistentemente.', 'tr. Tratndose de golpes,  dar,  propinar:  le zumbaron una buena paliza.'],
  zurda: ['adj. Que usa la mano izquierda del mismo modo que la mayora de las dems personas usan la derecha: not que era zurdo porque coga la cuchara con la izquierda. Tambin s.', 'Relativo a la mano o pierna izquierda. Tambin f.: me he hecho dao en la zurda.'],
  zurdo: ['adj. Que usa la mano izquierda del mismo modo que la mayora de las dems personas usan la derecha: not que era zurdo porque coga la cuchara con la izquierda. Tambin s.', 'Relativo a la mano o pierna izquierda. Tambin f.: me he hecho dao en la zurda.'],
